import Slider from 'react-slick';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import axios from 'axios';
import { useEffect, useState } from 'react';
import { useHistory } from 'react-router';
function Random(props){
    let history = useHistory();
    let [result, resultCg] = useState(JSON.parse(localStorage.getItem('random')));
    let [loading, setLoading] = useState(true);
    let type = [12, 14, 15, 25, 28, 32, 38, 39];
    let typeName = ['관광지','문화시설','행사','여행코스','레포츠','숙박','쇼핑','음식점'];

    useEffect(()=> {
        if(!localStorage.getItem('random')&& !props.val){
            setLoading(true);
            const test = async() => {
                let array = [];
                for(let i = 0; i< type.length; i++){
                    let res = await axios.get(`http://api.visitkorea.or.kr/openapi/service/rest/KorService/areaBasedList?ServiceKey=3gPkHl5ZAmSr7PbC2%2FMp3yKYSStNo4WjExafFNjc0GzixmJdjejAnpj2Liq3ZKw18yZPSEE9TGc6PQQaJlU%2BAA%3D%3D&arrange=Q&numOfRows=1&contentTypeId=${type[i]}&MobileOS=ETC&MobileApp=AppTest&_type=json`);
                    array.push(res.data.response.body.items.item);
                }
                localStorage.setItem('random', JSON.stringify(array));
                resultCg(array);
                setLoading(false);
            }
            test();
        }
        
    },[])

    const settings = {
        dots: true,  // 슬라이드 밑에 점 보이게
        infinite: true,  // 무한으로 반복
        speed: 500,
        autoplay: true,
        autoplaySpeed: 5000,  // 넘어가는 속도
        slidesToShow: 1,  // 4장씩 보이게
        slidesToScroll: 1,  // 1장씩 뒤로 넘어가게
        centerMode: true,
        centerPadding: '0px',
        customPaging: i => (  
            <div className="typeList list mt-3 mb-5">
                 # {!loading ? typeName[i] : null}
            </div>            
        )
      };
      return (
        <div className="random mt-3 mb-5">
          <h2 className="title mb-5">추천 관광지</h2>
          {
              !loading
              ? (
                <Slider {...settings}>
                <div className="random-img-box w-100 d-flex justify-content-center">
                    <img src={result[0].firstimage} className="random-img" alt="dd"
                    onClick={() => {
                        history.push(`/search/detail/${result[0].contentid}`);
                        props.typeCg(type[0]);
                        props.setLoading(true);
                        props.contentidCg(result[0].contentid);
                        localStorage.removeItem('result');
                        localStorage.removeItem('result2');
                        }}/>
                    <h3 className="titleName">{result[0].title}</h3>
                </div>
                <div className="random-img-box w-100 d-flex justify-content-center">
                    <img src={result[1].firstimage} className="random-img" alt="dd"
                    onClick={() => {
                        history.push(`/search/detail/${result[1].contentid}`);
                        props.typeCg(type[1]);
                        props.setLoading(true);
                        props.contentidCg(result[1].contentid);
                        localStorage.removeItem('result');
                        localStorage.removeItem('result2');
                        }}/>
                    <h3 className="titleName">{result[1].title}</h3>
                </div>
                <div className="random-img-box w-100 d-flex justify-content-center">
                    <img src={result[2].firstimage} className="random-img" alt="dd"
                    onClick={() => {
                        history.push(`/search/detail/${result[2].contentid}`);
                        props.typeCg(type[2]);
                        props.setLoading(true);
                        props.contentidCg(result[2].contentid);
                        localStorage.removeItem('result');
                        localStorage.removeItem('result2');
                        }}/>
                    <h3 className="titleName">{result[2].title}</h3>
                </div>
                <div className="random-img-box w-100 d-flex justify-content-center">
                    <img src={result[3].firstimage} className="random-img" alt="dd"
                    onClick={() => {
                        history.push(`/search/detail/${result[3].contentid}`);
                        props.typeCg(type[3]);
                        props.setLoading(true);
                        props.contentidCg(result[3].contentid);
                        localStorage.removeItem('result');
                        localStorage.removeItem('result2');
                        }}/>
                    <h3 className="titleName">{result[3].title}</h3>
                </div>
                <div className="random-img-box w-100 d-flex justify-content-center">
                    <img src={result[4].firstimage} className="random-img" alt="dd"
                    onClick={() => {
                        history.push(`/search/detail/${result[4].contentid}`);
                        props.typeCg(type[4]);
                        props.setLoading(true);
                        props.contentidCg(result[4].contentid);
                        localStorage.removeItem('result');
                        localStorage.removeItem('result2');
                        }}/>
                    <h3 className="titleName">{result[4].title}</h3>
                </div>
                <div className="random-img-box w-100 d-flex justify-content-center">
                    <img src={result[5].firstimage} className="random-img" alt="dd"
                    onClick={() => {
                        history.push(`/search/detail/${result[5].contentid}`);
                        props.typeCg(type[5]);
                        props.setLoading(true);
                        props.contentidCg(result[5].contentid);
                        localStorage.removeItem('result');
                        localStorage.removeItem('result2');
                        }}/>
                    <h3 className="titleName">{result[5].title}</h3>
                </div>
                <div className="random-img-box w-100 d-flex justify-content-center">
                    <img src={result[6].firstimage} className="random-img" alt="dd"
                    onClick={() => {
                        history.push(`/search/detail/${result[6].contentid}`);
                        props.typeCg(type[6]);
                        props.setLoading(true);
                        props.contentidCg(result[6].contentid);
                        localStorage.removeItem('result');
                        localStorage.removeItem('result2');
                        }}/>
                    <h3 className="titleName">{result[6].title}</h3>
                </div>
                <div className="random-img-box w-100 d-flex justify-content-center">
                    <img src={result[7].firstimage} className="random-img" alt="dd"
                    onClick={() => {
                        history.push(`/search/detail/${result[7].contentid}`);
                        props.typeCg(type[7]);
                        props.setLoading(true);
                        props.contentidCg(result[7].contentid);
                        localStorage.removeItem('result');
                        localStorage.removeItem('result2');
                        }}/>
                    <h3 className="titleName">{result[7].title}</h3>
                </div>
              </Slider>
              )
              : null
          }
         
        </div>
      );
}

export default Random;