/* eslint-disable */
import './sass/App.scss';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Container } from 'react-bootstrap';
import { Route, Switch, useHistory } from 'react-router-dom';
import NavInput from './components/NavInput';
import Main from './components/Main';
import Detail from './components/Detail';
import Random from './components/Random';

function App() {
  let history = useHistory();
  let [input, inputCg] = useState(localStorage.getItem('search'));
  let [val, valCg] = useState(localStorage.getItem('val'));
  let [result, resultCg] = useState();
  let [pageRe, pageReCg] = useState();
  let [type, typeCg]= useState(12);
  let [page, pageCg]= useState();
  let [loading, setLoading] = useState(false);
  let [areaCode, areaCodeCg] = useState(localStorage.getItem('areacode'));
  let [sigunguCode, sigunguCodeCg] = useState(localStorage.getItem('sigungucode'));
  let [category, categoryCg] = useState(localStorage.getItem('category'));
  let [contentid, contentidCg]= useState();
  useEffect(() => {
    setTimeout(()=> {
      const test = async() => {
        setLoading(true);
        try {
          if(!localStorage.getItem('search')) {
            history.push('/')
            if(window.location.href.split('#')[1] == '/'){
              localStorage.clear();
            }
          };
          let keyword = localStorage.getItem('val');
          let text = await axios.get(`http://api.visitkorea.or.kr/openapi/service/rest/KorService/searchKeyword?ServiceKey=3gPkHl5ZAmSr7PbC2%2FMp3yKYSStNo4WjExafFNjc0GzixmJdjejAnpj2Liq3ZKw18yZPSEE9TGc6PQQaJlU%2BAA%3D%3D&keyword=${keyword}&MobileOS=ETC&MobileApp=AppTest&arrange=A&pageNo=1&contentTypeId=${type}&_type=json`);
          let textRe = text.data.response.body.items.item;

          textRe.some(a => {
            let gap;
            if(a.addr1){
              gap = a.addr1;
            }else if(a.addr2){
              gap = a.addr2;
            }else if(a.title){
              gap = a.title;
            }
            if(gap.indexOf(val) !== -1){
              localStorage.setItem('areacode',a.areacode);
              areaCodeCg(localStorage.getItem('areacode'));
              localStorage.setItem('sigungucode',a.sigungucode);
              sigunguCodeCg(localStorage.getItem('sigungucode'));
              return (gap.indexOf(val) !== -1);
            }
          })            
          let res = await axios.get(`http://api.visitkorea.or.kr/openapi/service/rest/KorService/areaBasedList?ServiceKey=3gPkHl5ZAmSr7PbC2%2FMp3yKYSStNo4WjExafFNjc0GzixmJdjejAnpj2Liq3ZKw18yZPSEE9TGc6PQQaJlU%2BAA%3D%3D&contentTypeId=${type}&areaCode=${areaCode}&sigunguCode=${sigunguCode}&arrange=P&MobileOS=ETC&MobileApp=AppTest&_type=json`);
          resultCg(res.data.response.body.items.item);
          pageReCg(res.data.response.body);
          localStorage.setItem('category', "travel");
          setLoading(false);

        } catch (err) {
          setLoading(false);
        }      
      }
      test();
      return () => {
        cleanup
      }
    },1000)
    
  },[category, val]);

  useEffect(()=> {
    setLoading(true);
    const test = async() => {
      if(valCg){
        let res = await axios.get(`http://api.visitkorea.or.kr/openapi/service/rest/KorService/areaBasedList?ServiceKey=3gPkHl5ZAmSr7PbC2%2FMp3yKYSStNo4WjExafFNjc0GzixmJdjejAnpj2Liq3ZKw18yZPSEE9TGc6PQQaJlU%2BAA%3D%3D&contentTypeId=${type}&areaCode=${areaCode}&sigunguCode=${sigunguCode}&arrange=P&MobileOS=ETC&MobileApp=AppTest&_type=json`);
        // console.log(`http://api.visitkorea.or.kr/openapi/service/rest/KorService/areaBasedList?ServiceKey=3gPkHl5ZAmSr7PbC2%2FMp3yKYSStNo4WjExafFNjc0GzixmJdjejAnpj2Liq3ZKw18yZPSEE9TGc6PQQaJlU%2BAA%3D%3D&contentTypeId=${type}&areaCode=${areaCode}&sigunguCode=${sigunguCode}&arrange=P&MobileOS=ETC&MobileApp=AppTest&_type=json`);
        resultCg(res.data.response.body.items.item);
        pageReCg(res.data.response.body);
        setLoading(false);
      }
    }
    test();
  },[areaCode, sigunguCode, valCg, type])

  useEffect(() =>{
    setLoading(true);
    if(val && pageRe){     
      let pageCount = pageRe.totalCount / pageRe.numOfRows;
      let resultPage = [];
      for(let i = 1; i <= Math.ceil(pageCount); i++){
        resultPage.push(i);
      }
      // console.log(pageRe.totalCount);
      pageCg(resultPage);
      localStorage.setItem('page', JSON.stringify(resultPage));        
      if(val && window.location.href.split('/')[5] != 'detail') {    
        localStorage.removeItem('result');
        localStorage.removeItem('result2');
        history.push(`/search/${!category ? "travel" : category}/${parseInt(pageRe.pageNo)}`);
        
      }
      setLoading(false);      
    }
  },[areaCode, sigunguCode, type, val, category, pageRe]);    
  
  return (
    <div className="App">
      <Container fluid="sm" className="container">
        <Switch>          
          <Route path={`/search/${!category ? "travel" : category}/:id`}> 
            <NavInput 
                input={input} 
                inputCg={inputCg} 
                val={val} 
                valCg={valCg} 
                typeCg={typeCg}
                categoryCg={categoryCg}
              ></NavInput> 
              <Main 
                loading={loading} 
                setLoading={setLoading}
                result={result} 
                page={page} 
                val={val} 
                type={type}
                typeCg={typeCg} 
                category={category}
                categoryCg={categoryCg}
                areaCode={areaCode}
                sigunguCode={sigunguCode}
                contentidCg={contentidCg}
                ></Main>
            </Route> 
            <Route path="/search/detail/:id">
              <NavInput 
                input={input} 
                inputCg={inputCg} 
                val={val} 
                valCg={valCg} 
                typeCg={typeCg}
                categoryCg={categoryCg}
              ></NavInput> 
              <Detail val={val} contentid={contentid} type={type} loading={loading} setLoading={setLoading}></Detail>
            </Route>   
            <Route path="/">
            <NavInput 
              input={input} 
              inputCg={inputCg} 
              val={val} 
              valCg={valCg}
              typeCg={typeCg}
              categoryCg={categoryCg} 
            ></NavInput>  
            <Random val={val} typeCg={typeCg} contentidCg={contentidCg} setLoading={setLoading}></Random>
          </Route> 
        </Switch> 
                         
      </Container>      
    </div> 
  );
}

export default App;
