/* eslint-disable */
import axios from 'axios';
import { useEffect, useState, memo } from 'react';
import { ListGroup, Spinner, Nav } from 'react-bootstrap';
import { useHistory, useParams } from 'react-router-dom';
import TravelList from './TravelList';
function Main(props) {    
    let { id }= useParams();
    let history = useHistory();
    let [result, resultCg] = useState(props.result);
    let [num, numCg] = useState({ start : 0, end : 5 });
    let [arr, arrCg] = useState();
    let [btnPush, btnPushCg] = useState(false);
    let [loading, setLoading] = useState(false);
    let [page, pageCg] = useState(JSON.parse(localStorage.getItem('page')));

    useEffect(() => {
        setTimeout(()=>{
            const test = async() => {
                setLoading(true);
                try {
                    let res = await axios.get(`http://api.visitkorea.or.kr/openapi/service/rest/KorService/areaBasedList?ServiceKey=3gPkHl5ZAmSr7PbC2%2FMp3yKYSStNo4WjExafFNjc0GzixmJdjejAnpj2Liq3ZKw18yZPSEE9TGc6PQQaJlU%2BAA%3D%3D&contentTypeId=${props.type}&areaCode=${props.areaCode}&sigunguCode=${props.sigunguCode}&arrange=P&pageNo=${id}&MobileOS=ETC&MobileApp=AppTest&_type=json`);
                    resultCg(res.data.response.body.items.item);
                    pageCg(JSON.parse(localStorage.getItem('page')));             
                    setLoading(false);                
                } catch (error) {
                     console.log(error);
                }      
            }        
        
            test();
            return () => {          
                test();
                
            }
        },100)
      },[props.page, props.areaCode, props.sigunguCode, props.type, props.result, props.val, id]);
      
    // useEffect(()=> {
    //     if(page){      
    //         numCg({start:0,end:5});
    //         let target = page.slice(num.start,num.end);        
    //         arrCg([...target]);
    //         console.log(target);
    //     }
    // },[props.pageCg, props.type, props.result, props.val, props.category])

    //   useEffect(()=> {
    //     if(pageCg){      
    //         // btnPushCg(true);    
    //         let target = page.slice(num.start,num.end);
    //         arrCg([...target]);
    //         if(btnPushCg) {
    //             setTimeout(()=> {
    //                 history.push(`/search/${props.category}/${page[0]}`);    
    //                 btnPushCg(false);  
    //             },100)  
    //         }                                      
    //     }            
    //   },[props.pageCg, props.type, props.result, props.val, props.category, btnPush, num])

      let typeChange = (type, category, e) => {
        props.typeCg(type); 
        props.categoryCg(category);
        e.preventDefault();
      }
    
    return (
        <div className="mainList mt-5">
            {
                props.val
                ? (<Nav fill variant="tabs" defaultActiveKey={`/search/${window.location.href.split('/')[5]}/`} className="menu d-flex justify-content-around m-auto border-bottom-0">
                <Nav.Item>
                    <Nav.Link id="first_btn" className="cate-list" href="/search/travel/" onClick={(e) => {
                        localStorage.setItem('category', "travel");
                        typeChange(12,localStorage.getItem('category'),e);
                        }}>여행지</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link className="cate-list" href="/search/cultural/"onClick={(e) => {
                        localStorage.setItem('category', "cultural");
                        typeChange(14,localStorage.getItem("category"),e);
                        }}>문화시설</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link className="cate-list" href="/search/event/"onClick={(e) => {
                        localStorage.setItem('category', "event");
                        typeChange(15,localStorage.getItem("category"),e);
                    }}>행사</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link className="cate-list" href="/search/travelCourse/" onClick={(e) => {
                        localStorage.setItem('category', "travelCourse");
                        typeChange(25,localStorage.getItem("category"),e);
                    }}>여행코스</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link className="cate-list" href="/search/sports/" onClick={(e) => {
                        localStorage.setItem('category', "sports");
                        typeChange(28,localStorage.getItem("category"),e);
                    }}>레포츠</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link className="cate-list" href="/search/lodgment/" onClick={(e) => {
                        localStorage.setItem('category', "lodgment");
                        typeChange(32,localStorage.getItem("category"),e);
                    }}>숙박</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link className="cate-list" href="/search/shopping/" onClick={(e) => {
                        localStorage.setItem('category', "shopping");
                        typeChange(38,localStorage.getItem("category"),e);
                    }}>쇼핑</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link className="cate-list" to="/search/eat/" onClick={(e) => {
                        localStorage.setItem('category', "eat");
                        typeChange(39,localStorage.getItem("category"),e);
                    }}>음식점</Nav.Link>
                </Nav.Item>
                </Nav>)
                :null
            } 
            <ListGroup defaultActiveKey="#link" className="listGp w-100 p-0">
            {props.loading || loading? (
            <Spinner className="m-auto" animation="border" role="status">
            <span className="visually-hidden">Loading...</span>
            </Spinner>
            ) : result != null
            ? Array.isArray(result)
                ?(
                    result.map((a, i) => (
                        <TravelList key={i} list={a} index={i} contentidCg={props.contentidCg} setLoading={props.setLoading}></TravelList>
                    ))
                )
                : (
                    <TravelList key="1" list={result} index="0" contentidCg={props.contentidCg} setLoading={props.setLoading}></TravelList>
                )
            : (<div className="text-center">검색결과가 없습니다.</div>) 
            }
            </ListGroup>
            <div className="list-box">
                {/* <button className="list-btn" onClick={() => {           
                    let newArray = num;
                    if(newArray.start > 1){
                        newArray.start = newArray.start - 5;
                        newArray.end = newArray.end - 5;
                        // console.log(arr.length);                    
                        numCg(newArray);
                        btnPushCg(true);
                    }                    
                }}>이전</button> */}
                <ul className="listNum p-0">
                {
                    page != null
                    ? (page.map((a,i) => (
                            <li className="num" key={i} onClick={() => {
                            history.push(`/search/${props.category}/${page[i]}`)
                            // btnPushCg(true);
                            }}>{a}</li>
                        )))       
                        
                    : null
                }
                </ul> 
                {/* <button className="list-btn" onClick={() => {           
                    let newArray = num;
                    if(newArray.end < props.page.length){
                        newArray.start = newArray.start + 5;
                        newArray.end = newArray.end + 5;
                        // console.log(props.page.length);                    
                        numCg(newArray);
                        btnPushCg(true);
                    }                  
                }}>다음</button> */}
            </div>            
        </div>
    )
}

export default Main;