import { ListGroup, Row, Col } from 'react-bootstrap';
import { useHistory } from 'react-router';
function TravelList(props) {
    let history = useHistory();
    return (
        <ListGroup.Item key={props.index} action onClick={() => {
            history.push(`/search/detail/${props.list.contentid}`);
            props.setLoading(true);
            props.contentidCg(props.list.contentid);
            localStorage.removeItem('result');
            localStorage.removeItem('result2');  
            }}>
            <Row className="list text-left">
            <Col className="img-box d-flex align-items-center" sm={3}>
                {
                    props.list.firstimage2 != null
                    ? (<img className="img w-100 rounded" src={props.list.firstimage2} alt={props.index}/>)
                    : (<div className="img noImg w-100 rounded d-flex align-items-center justify-content-center font-weight-bolder">No Image</div>)
                }                      
            </Col>
            <Col sm={7} className="list-text p-3">
                <p className="title">{props.list.title}</p>
                <p className="address">{props.list.addr1}</p>
            </Col>                                     
            <Col sm={2} className="m-auto text-center"><div className="btn border border-secondary">더보기</div></Col>
            </Row>                  
        </ListGroup.Item>
    )
}

export default TravelList;