import { InputGroup, FormControl, Button, Nav } from 'react-bootstrap';
import { useHistory } from 'react-router';


function NavInput(props) {
  let history = useHistory();
  const onChange = e => {
    props.inputCg(e.target.value);
  }
  const onReset = () => {
    props.valCg("");
    localStorage.clear();
  }
    return (
      <div className="nav-bar">
        <Nav.Link className="main-logo" href="/"><h1 className="p-3 logo" onClick={onReset}>Travel</h1></Nav.Link>    
        <InputGroup className="inputGp w-50">
          <FormControl
            placeholder="장소를 검색하세요"
            aria-label="Recipient's username"
            aria-describedby="basic-addon2"
            onChange={onChange} onKeyPress={(e) => {if(window.event.keyCode===13){
              localStorage.clear();
              localStorage.setItem('search',props.input);
              props.valCg(localStorage.getItem('search'));
              localStorage.setItem('val',encodeURI(props.input));
              localStorage.setItem('category', "travel");
              props.typeCg(12); 
              props.categoryCg(localStorage.getItem('category'));
              e.preventDefault();
              history.push(`/search/travel/1`);
            }}}
          />
          <Button variant="outline-secondary" className="input-btn" onClick={(e) => {
            localStorage.clear();
            localStorage.setItem('search',props.input);
            props.valCg(localStorage.getItem('search'));
            localStorage.setItem('val',encodeURI(props.input));
            localStorage.setItem('category', "travel");
            props.typeCg(12); 
            props.categoryCg(localStorage.getItem('category'));
            e.preventDefault();
          }}>
            검색
          </Button>
        </InputGroup>
        <div className="bin-box">
        </div>        
      </div>
    )
}

export default NavInput;