/*eslint-disable*/
import { cleanup } from '@testing-library/react';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { useHistory } from 'react-router';
const { kakao } = window;
function Detail(props) {
    let history = useHistory();
    let [result, resultCg] = useState(JSON.parse(localStorage.getItem('result')));
    let [result2, result2Cg] = useState(JSON.parse(localStorage.getItem('result2')));
    useEffect(() => {
            const test = async() => {
                console.log(props.contentid);
                let res = await axios.get(`http://api.visitkorea.or.kr/openapi/service/rest/KorService/detailCommon?ServiceKey=3gPkHl5ZAmSr7PbC2%2FMp3yKYSStNo4WjExafFNjc0GzixmJdjejAnpj2Liq3ZKw18yZPSEE9TGc6PQQaJlU%2BAA%3D%3D&contentId=${props.contentid ? props.contentid : JSON.parse(localStorage.getItem('result')).contentid}&defaultYN=Y&firstImageYN=Y&addrinfoYN=Y&overviewYN=Y&mapinfoYN=Y&MobileOS=ETC&MobileApp=AppTest&_type=json`);
                let res2 = await axios.get(`http://api.visitkorea.or.kr/openapi/service/rest/KorService/detailIntro?ServiceKey=3gPkHl5ZAmSr7PbC2%2FMp3yKYSStNo4WjExafFNjc0GzixmJdjejAnpj2Liq3ZKw18yZPSEE9TGc6PQQaJlU%2BAA%3D%3D&contentId=${props.contentid ? props.contentid : JSON.parse(localStorage.getItem('result')).contentid}&contentTypeId=${props.type ? props.type : JSON.parse(localStorage.getItem('result')).contenttypeid}&MobileOS=ETC&MobileApp=AppTest&_type=json`);
                if(result == null || result2 == null){
                    window.localStorage.setItem('result', JSON.stringify(res.data.response.body.items.item));
                    window.localStorage.setItem('result2', JSON.stringify(res2.data.response.body.items.item));               
                    
                }
                resultCg(JSON.parse(localStorage.getItem('result')));
                result2Cg(JSON.parse(localStorage.getItem('result2'))); 
                history.push(`/search/detail/${JSON.parse(localStorage.getItem('result')).contentid}`);
                props.setLoading(false);
                
            }
            test();
            return () => {
                cleanup
            }
        
    },[])

    useEffect(() => {
        if(!props.loading && result.addr1) {
            function MapWalker(position){

                //커스텀 오버레이에 사용할 map walker 엘리먼트
                var content = document.createElement('div');
                var figure = document.createElement('div');
                var angleBack = document.createElement('div');
            
                //map walker를 구성하는 각 노드들의 class명을 지정 - style셋팅을 위해 필요
                content.className = 'MapWalker';
                figure.className = 'figure';
                angleBack.className = 'angleBack';
            
                content.appendChild(angleBack);
                content.appendChild(figure);
            
                //커스텀 오버레이 객체를 사용하여, map walker 아이콘을 생성
                var walker = new kakao.maps.CustomOverlay({
                    position: position,
                    content: content,
                    yAnchor: 1
                });
            
                this.walker = walker;
                this.content = content;
            }
            
            //로드뷰의 pan(좌우 각도)값에 따라 map walker의 백그라운드 이미지를 변경 시키는 함수
            //background로 사용할 sprite 이미지에 따라 계산 식은 달라 질 수 있음
            MapWalker.prototype.setAngle = function(angle){
            
                var threshold = 22.5; //이미지가 변화되어야 되는(각도가 변해야되는) 임계 값
                for(var i=0; i<16; i++){ //각도에 따라 변화되는 앵글 이미지의 수가 16개
                    if(angle > (threshold * i) && angle < (threshold * (i + 1))){
                        //각도(pan)에 따라 아이콘의 class명을 변경
                        var className = 'm' + i;
                        this.content.className = this.content.className.split(' ')[0];
                        this.content.className += (' ' + className);
                        break;
                    }
                }
            };
            
            //map walker의 위치를 변경시키는 함수
            MapWalker.prototype.setPosition = function(position){
                this.walker.setPosition(position);
            };
            
            //map walker를 지도위에 올리는 함수
            MapWalker.prototype.setMap = function(map){
                this.walker.setMap(map);
            };
            var mapContainer = document.getElementById('map'), // 지도를 표시할 div 
                mapOption = {
                    center: new kakao.maps.LatLng(33.450701, 126.570667), // 지도의 중심좌표
                    level: 5 // 지도의 확대 레벨
                };  

                // 지도를 생성합니다    
                var map = new kakao.maps.Map(mapContainer, mapOption); 

                map.addOverlayMapTypeId(kakao.maps.MapTypeId.ROADVIEW);

                var roadviewContainer = document.getElementById('roadview'); // 로드뷰를 표시할 div
                var roadview = new kakao.maps.Roadview(roadviewContainer); // 로드뷰 객체
                var roadviewClient = new kakao.maps.RoadviewClient();
    
                // 주소-좌표 변환 객체를 생성합니다
                var geocoder = new kakao.maps.services.Geocoder();
    
                // 주소로 좌표를 검색합니다
                geocoder.addressSearch(`${result.addr1}`, function(result, status) {
    
                    // 정상적으로 검색이 완료됐으면 
                    if (status === kakao.maps.services.Status.OK) {
    
                        var coords = new kakao.maps.LatLng(result[0].y, result[0].x);
    
                        // 결과값으로 받은 위치를 마커로 표시합니다
                        // var marker = new kakao.maps.Marker({
                        //     map: map,
                        //     position: coords
                        // });

                        roadviewClient.getNearestPanoId(coords, 50, function(panoId) {
                            roadview.setPanoId(panoId, coords); //panoId와 중심좌표를 통해 로드뷰 실행
                        });
    
                        // 지도의 중심을 결과값으로 받은 위치로 이동시킵니다
                        map.setCenter(coords);

                        var mapWalker = null;

                    // 로드뷰의 초기화 되었을때 map walker를 생성한다.
                    kakao.maps.event.addListener(roadview, 'init', function() {

                        // map walker를 생성한다. 생성시 지도의 중심좌표를 넘긴다.
                        mapWalker = new MapWalker(coords);
                        mapWalker.setMap(map); // map walker를 지도에 설정한다.

                        // 로드뷰가 초기화 된 후, 추가 이벤트를 등록한다.
                        // 로드뷰를 상,하,좌,우,줌인,줌아웃을 할 경우 발생한다.
                        // 로드뷰를 조작할때 발생하는 값을 받아 map walker의 상태를 변경해 준다.
                        kakao.maps.event.addListener(roadview, 'viewpoint_changed', function(){

                            // 이벤트가 발생할 때마다 로드뷰의 viewpoint값을 읽어, map walker에 반영
                            var viewpoint = roadview.getViewpoint();
                            mapWalker.setAngle(viewpoint.pan);

                        });

                        // 로드뷰내의 화살표나 점프를 하였을 경우 발생한다.
                        // position값이 바뀔 때마다 map walker의 상태를 변경해 준다.
                        kakao.maps.event.addListener(roadview, 'position_changed', function(){

                            // 이벤트가 발생할 때마다 로드뷰의 position값을 읽어, map walker에 반영 
                            var position = roadview.getPosition();
                            mapWalker.setPosition(position);
                            map.setCenter(coords);

                        });
                    });
                    } 
                }); 
                
            }
        
        
        return () => {};
    }, [props.loading]);
    return (
       <div className="detail mt-5">
           {
               !props.loading
               ? (
                <>
                    <p className="title">{result.title}</p>
                    <div className="Main m-3">
                        <img className="img" src={result.firstimage} alt="대표이미지" />
                        <div className="overView m-5 w-50" dangerouslySetInnerHTML={{__html : result.overview}}></div>
                    </div>          
                    <div className="title">상세정보</div>  
                    <ul className="item pt-4 pb-4">
                        <li className="list">문의 및 안내 전화번호</li>
                        <p>
                            {
                                props.type == 12 ? result2.infocenter
                                : props.type == 14 ? result2.infocenterculture
                                : props.type == 15 ? result2.sponsor1tel
                                : props.type == 25 ? result2.infocentertourcourse
                                : props.type == 28 ? result2.infocenterleports
                                : props.type == 32 ? result2.infocenterlodg
                                : props.type == 38 ? result2.infocentershopping
                                : props.type == 39 ? result2.infocenterfood : null
                            }
                        </p>
                        <li className="list">홈페이지 링크</li>
                        <p dangerouslySetInnerHTML={{__html : result.homepage}}></p>
                        <li className="list">주소</li>
                        <p>{result.addr1}</p>
                        <li className="list">이용시간</li>
                        <p dangerouslySetInnerHTML={{__html :                         
                            props.type == 12 ? result2.usetime
                            : props.type == 14 ? result2.usetimeculture
                            : props.type == 15 ? result2.playtime
                            : props.type == 25 ? result2.taketime
                            : props.type == 28 ? result2.usetimeleports
                            : props.type == 32 ? null
                            : props.type == 38 ? result2.opentime
                            : props.type == 39 ? result2.opentimefood : null
                        }}>                            
                        </p>
                        <li className="list">휴일</li>
                        <p dangerouslySetInnerHTML={{__html :
                            props.type == 12 ? result2.restdate
                            : props.type == 14 ? result2.restdateculture
                            : props.type == 15 ? null
                            : props.type == 25 ? null
                            : props.type == 28 ? result2.restdateleports
                            : props.type == 32 ? null
                            : props.type == 38 ? result2.restdateshopping
                            : props.type == 39 ? result2.restdatefood : null
                        }}>
                        </p>
                        <li className="list">주차 여부</li>
                        <p dangerouslySetInnerHTML={{__html :
                            props.type == 12 ? result2.parking
                            : props.type == 14 ? result2.parkingculture
                            : props.type == 15 ? null
                            : props.type == 25 ? null
                            : props.type == 28 ? result2.restdateleports
                            : props.type == 32 ? result2.parkinglodging
                            : props.type == 38 ? result2.parkingshopping
                            : props.type == 39 ? result2.parkingfood : null
                        }}>
                        </p>
                        <li className="list">이용요금</li>
                        <p dangerouslySetInnerHTML={{__html :
                            props.type == 12 ? result2.usefee
                            : props.type == 14 ? result2.usefee
                            : props.type == 15 ? result2.usetimefestival
                            : props.type == 25 ? null
                            : props.type == 28 ? result2.usefeeleports
                            : props.type == 32 ? null
                            : props.type == 38 ? null
                            : props.type == 39 ? null : null
                        }}>
                        </p>
                    </ul>        
                    <p className="title">위치지도</p>
                    {/* <div className="mt-3 m-auto" id="map" style={{ width: "90%", height: "300px" }}></div>
                    <div className="mt-3 mb-5 m-auto" id="roadview" style={{width: "90%", height: "300px" }}></div> */}
                    <div class="map_wrap">
                        <div id="mapWrapper" style={{ width: "50%", height: "300px", float:"left"}}>
                            <div id="map" style={{ width: "100%", height: "100%" }}></div>
                        </div>
                        <div id="rvWrapper" style={{ width: "50%", height: "300px", float: "left"}}>
                            <div id="roadview" style={{ width: "100%", height: "100%" }}></div>
                        </div>
                    </div>
                </>
               )
               :null
           }
           
       </div>
    )
}

export default Detail;